<style scoped>
#footer {
  border-top: 1px solid rgb(212, 212, 212);
  margin: 3rem -3.25rem 0 -3.25rem;
}
.goTop {
  border-radius: 50%;
  border: 1px solid black;
  overflow: hidden;
  padding: 10px;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.goTop svg {
  width: 25px;
  height: 25px;
}

@media screen and (max-width: 600px) {
  .goTop{
    bottom: 20px;
    right: 20px;
    position:fixed;
  }

  #footer{
    margin: 3rem 0rem 0 0rem;
  }

  .footer-padding {
    padding: 2.25rem 0.5rem;
  } 
  #footer .justify-content-between{
    justify-content: center;
  }
}
</style>
<template>
  <div id="footer">
    <div class="footer-padding d-flex justify-content-between">
      <div>© 2020 Jason Hong. All rights reserved.</div>
      <div class="goTop" @click="scrollTop">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="1.5"
            d="M8 7l4-4m0 0l4 4m-4-4v18"
          ></path>
        </svg>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  setup(props) {
    const scrollTop = () => {
      document.getElementById("right-content").scrollTo({
        top: 200,
        left: 0,
        behavior: "smooth",
      });
    };

    return {scrollTop}
  },
};
</script>
