<style scoped>
#LeftAsideBar {
  position: relative;
  left: 0;
  width: 8rem;
  border-right: 1px solid rgb(212, 212, 212);
  padding: 1.25rem;
  height: 100vh;
}

.fullMenu {
  width: 0%;
  height: 0%;
  transition: 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: white;
  z-index: 20;
}

.fullMenu a {
  padding: 1.5rem;
  font-size: 18px;
}

.fullMenu a:hover {
  background: rgb(233, 233, 233);
}

.fullMenu.active {
  position: fixed;
  left: 9rem;
  top: 0;
  width: calc(100% - 9rem);
  height: 100%;
}

.st0 {
  fill: #7f1a1e;
}
.st1 {
  fill: #3e3a39;
}

.Logo{
  text-align: center;
}
.Logo svg {
  width: 80px;
  height: 80px;
}

.menu {
  height: 35px;
  width: 35px;
  position: relative;
  border-radius: 100%;
  transition: 0.3s;
  cursor: pointer;
}

.mod{
  position: fixed;
    bottom: 0%;
    left: 4.5rem;
    transform: translate(-70%,-100%);
}

.bar {
  height: 5px;
  width: 30px;
  display: block;
  margin: 2px auto;
  position: relative;
  background-color: black;
  -moz-border-radius: 10px;
  -webkit-border-radius: 10px;
  border-radius: 10px;
  -moz-transition: 0.4s;
  -o-transition: 0.4s;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.model-1 .bar {
  position: absolute;
}
.model-1 .bar:nth-of-type(1) {
  top: 0px;
  -moz-transition: top 0.3s ease 0.3s, -moz-transform 0.3s ease-out 0.1s;
  -o-transition: top 0.3s ease 0.3s, -o-transform 0.3s ease-out 0.1s;
  -webkit-transition: top 0.3s ease, -webkit-transform 0.3s ease-out;
  -webkit-transition-delay: 0.3s, 0.1s;
  transition: top 0.3s ease 0.3s, transform 0.3s ease-out 0.1s;
  -moz-animation: mrotr 2s cubic-bezier(0.5, 0.2, 0.2, 1.01);
  -webkit-animation: mrotr 2s cubic-bezier(0.5, 0.2, 0.2, 1.01);
  animation: mrotr 2s cubic-bezier(0.5, 0.2, 0.2, 1.01);
}
.model-1 .bar:nth-of-type(2) {
  top: 10px;
  -moz-transition: ease 0.3s 0.3s;
  -o-transition: ease 0.3s 0.3s;
  -webkit-transition: ease 0.3s;
  -webkit-transition-delay: 0.3s;
  transition: ease 0.3s 0.3s;
  -moz-animation: fade 2s cubic-bezier(0.5, 0.2, 0.2, 1.01);
  -webkit-animation: fade 2s cubic-bezier(0.5, 0.2, 0.2, 1.01);
  animation: fade 2s cubic-bezier(0.5, 0.2, 0.2, 1.01);
}
.model-1 .bar:nth-of-type(3) {
  top: 20px;
  -moz-transition: top 0.3s ease 0.3s, -moz-transform 0.3s ease-out 0.1s;
  -o-transition: top 0.3s ease 0.3s, -o-transform 0.3s ease-out 0.1s;
  -webkit-transition: top 0.3s ease, -webkit-transform 0.3s ease-out;
  -webkit-transition-delay: 0.3s, 0.1s;
  transition: top 0.3s ease 0.3s, transform 0.3s ease-out 0.1s;
  -moz-animation: mrotl 2s cubic-bezier(0.5, 0.2, 0.2, 1.01);
  -webkit-animation: mrotl 2s cubic-bezier(0.5, 0.2, 0.2, 1.01);
  animation: mrotl 2s cubic-bezier(0.5, 0.2, 0.2, 1.01);
}
.model-1 .menu.active .bar:nth-of-type(1) {
  top: 15px;
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  -moz-transition: top 0.3s ease 0.1s, -moz-transform 0.3s ease-out 0.5s;
  -o-transition: top 0.3s ease 0.1s, -o-transform 0.3s ease-out 0.5s;
  -webkit-transition: top 0.3s ease, -webkit-transform 0.3s ease-out;
  -webkit-transition-delay: 0.1s, 0.5s;
  transition: top 0.3s ease 0.1s, transform 0.3s ease-out 0.5s;
}
.model-1 .menu.active .bar:nth-of-type(2) {
  opacity: 0;
}
.model-1 .menu.active .bar:nth-of-type(3) {
  top: 15px;
  -moz-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  -moz-transition: top 0.3s ease 0.1s, -moz-transform 0.3s ease-out 0.5s;
  -o-transition: top 0.3s ease 0.1s, -o-transform 0.3s ease-out 0.5s;
  -webkit-transition: top 0.3s ease, -webkit-transform 0.3s ease-out;
  -webkit-transition-delay: 0.1s, 0.5s;
  transition: top 0.3s ease 0.1s, transform 0.3s ease-out 0.5s;
}

@media screen and (max-width:600px){
  #LeftAsideBar{
    display: none;
  }
  .mod{
    right: 30px;
    left: unset;
    top: 30px;
    bottom:unset;
    transform: none;
    z-index: 99;
  }
  .fullMenu.active {
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}
}

