<script>
import { onMounted, ref } from "vue";
export default {
  setup() {
    const isOpen = ref(false);
    const isShow = ref(false);
    const handOpen = () => {
      isOpen.value = !isOpen.value;
    };

    onMounted(() => {
      document.getElementById("right-content").addEventListener(
        "scroll",
        () => {
          let scroll = document.getElementById("right-content").scrollTop
          if(scroll > 110){
            isShow.value = true
          } else{
            isShow.value = false
          }
        },
        true
      );
    });
    return { isOpen, handOpen,isShow };
  },
};
</script>
